import {
  writable,
  derived
} from 'svelte/store';
import {
  get
} from '../utils/ajax';


const AUTH_TOKEN_KEY = 'auth_token_key';

function createAuth() {
  const defaultAuth = {
    user: null,
    token: '',
  }

  const storedToken = JSON.parse(sessionStorage.getItem(AUTH_TOKEN_KEY));

  const {
    subscribe,
    set
  } = writable(storedToken || defaultAuth);

  return {
    subscribe,
    login: (user, token) => {
      sessionStorage.setItem(AUTH_TOKEN_KEY, JSON.stringify({
        user,
        token
      }));

      set({
        user,
        token
      });
    },
    logout: () => {
      sessionStorage.removeItem(AUTH_TOKEN_KEY);
      set(defaultAuth);
    }
  };
}

export const auth = createAuth();
export const isLoggedIn = derived(auth, ({
  token
}) => Boolean(token));

function cacheResults(fetchData, clearStore) {
  let cachedItems = [];
  let cached = 0;

  return derived([cache, auth], async ([cacheBust, {
    token
  }], set) => {
    if (cached !== cacheBust) {
      cachedItems = [];
      cached = cacheBust;
    }
    if (token) {
      cachedItems = cachedItems.length ? cachedItems : await fetchData(token);
    } else {
      cachedItems = [];
    }
    set(cachedItems);
  }, []);
}

function cacheBuster() {
  const {
    subscribe,
    set
  } = writable(0);

  return {
    subscribe,
    clear: () => set(Math.random())
  }
}

export const cache = cacheBuster();
export const testStore = derived([cache, auth], ([_, $auth]) => {
  console.log('callback', $auth);
  return 'updated'
})

export const games = cacheResults((token) => get('/games', token));
export const friends = cacheResults((token) => get('/friends', token));
export const statuses = cacheResults((token) => get('/activities/statuses', token));

